.card-btn {
  outline: none;
  border: none;
  border-radius: 5px;
  width: 190px;
  height: 45px;
  background-color: #C4C4C4;
  cursor: pointer;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  line-height: 21px;

  &:hover {
    background-color: #c3c4ac;
    color: #1b353e;
  }

  &:active {
    background-color: #a6c4b2;
    color: #1b3e3c;
  }
}
